<template>
  <div class="home">
    <Intro class="pt-5" msg="Block-Informer" />
    <Maincontent
      txt="Latest News"
      :title-one="title1"
      :title-two="title2"
      :title-three="title3"
      :desc-one="description1"
      :desc-two="description2"
      :desc-three="description3"
      :src-one="source1"
      :src-two="source2"
      :src-three="source3"
      :lnk-one="link1"
      :lnk-two="link2"
      :lnk-three="link3"
    />
    <Pricechart />
    <div class="container">
      <Footer id="foot" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Intro from "@/components/Intro.vue";
import Maincontent from "@/components/Maincontent.vue";
import Pricechart from "@/components/Pricechart.vue";
import Footer from "@/components/Footer.vue";
export default {
  data() {
    return {
      title1: "",
      description1: "",
      link1: null,
      source1: "",
      title2: "",
      description2: "",
      link2: null,
      source2: "",
      title3: "",
      description3: "",
      link3: null,
      source3: "",
    };
  },
  name: "Home",
  components: {
    Intro,
    Maincontent,
    Pricechart,
    Footer,
  },
  methods: {
    async getNews() {
      let titlearray = [];
      let linkarray = [];
      let sourcearray = [];
      let descarray = [];
      // console.log(process.env.KEY)
      const res = await fetch(
        `https://cors-anywhere.herokuapp.com/https://newsdata.io/api/1/news?apikey=${process.env.VUE_APP_KEY}&q=bitcoin&language=en&category=Business`
      );
      const data = await res.json();
      // console.log(data);
      for (let i = 0; i < 10; i++) {
        if (data.results[i].description !== null) {
          titlearray.push(data.results[i].title);
          linkarray.push(data.results[i].link);
          descarray.push(data.results[i].description);
          sourcearray.push(data.results[i].source_id);
        }
        // console.log(data.results[i]);
      }
      this.title1 = titlearray[0];
      this.description1 = descarray[0];
      this.link1 = linkarray[0];
      this.source1 = sourcearray[0];
      this.title2 = titlearray[1];
      this.description2 = descarray[1];
      this.link2 = linkarray[1];
      this.source2 = sourcearray[1];
      this.title3 = titlearray[2];
      this.description3 = descarray[2];
      this.link3 = linkarray[2];
      this.source3 = sourcearray[2];
      // console.log(this.title1);
      // console.log(this.description1);
      // console.log(this.link1);
      // console.log(this.source1);
      // console.log(this.title2);
      // console.log(this.description2);
      // console.log(this.link2);
      // console.log(this.source2);
      // console.log(this.title3);
      // console.log(this.description3);
      // console.log(this.link3);
      // console.log(this.source3);
      //  console.log(titlearray)
      //  console.log(linkarray)
      //  console.log(sourcearray)
      //  console.log(descarray)
    },
  },
  mounted() {
    this.getNews();
  },
};
</script>
