<template>
  <h1 class="display-3 mt-5 mb-5">Rankings</h1>
  <div id="outcon" class="container mt-5">
    <div id="tablecon" class="table-responsive">
      <!-- <div v-for="eachcoin in dataarr" :key="eachcoin.id" class="coins">
<img :src="eachcoin.icon" :alt="eachcoin.name">
<h5>{{eachcoin.name}}</h5>
</div> -->
      <table id="tablez" class="table table-borderless">
        <thead>
          <tr id="titlerow">
            <th scope="col">Symbol</th>
            <th scope="col">Name</th>
            <th scope="col">Icon</th>
            <th scope="col">Price</th>
            <th scope="col">Rank</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="eachcoin in dataarr" :key="eachcoin.id">
            <th scope="row">{{ eachcoin.symbol }}</th>
            <td>{{ eachcoin.name }}</td>
            <td><img :src="eachcoin.icon" :alt="eachcoin.id" /></td>
            <td>{{ eachcoin.priceBtc }}</td>
            <td>{{ eachcoin.rank }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dataarr: null,
    };
  },
  name: "Pricechart",
  methods: {
    async getCoinData() {
      const res = await fetch(
        "https://api.coinstats.app/public/v1/coins?skip=0&limit=10"
      );
      const data = await res.json();
      this.dataarr = data.coins;
      console.log(this.dataarr);
      //   console.log(data.coins)
    },
  },
  created() {
    this.getCoinData();
  },
};
</script>
<style scoped>
.coins {
  text-align: center;
}
img {
  width: 50px;
  height: auto;
}
.table .table-borderless {
  text-align: center;
  color: #f29d0a;
  text-shadow: 0px 0px 20px;
}
#tablez {
  text-align: center;
  color: #f29d0a;
  text-shadow: 0px 0px 20px;
}
#outcon {
  box-shadow: 0px 0px 20px 20px #ffc10726;
  border-radius: 25px;
}
h1 {
  color: white;
  text-align: center;
}
#titlerow {
  color: #fff8dccc;
  font-family: "Merriweather", serif;
}
#tablecon {
  padding: 20px;
}
* {
  font-family: "Merriweather Regular 400", serif;
}
</style>